___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: grid;
  background-color: #111;
  grid-template-columns: 1.5fr 8.5fr;
  height: 100vh;
  padding: 120px 160px;
}

nav {
  min-width: 220px;
  background-color: #111;
  grid-row: 1/2;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 48px 12px;
}

nav h1 {
  font-size: 16px;
  font-weight: 600;
  margin-left: 12px;
  color: #aaa;
}

.tab-group {
  display: flex;
  flex-direction: column;
  margin-top: 16px;
  gap: 4px;
}

.project-tab {
  padding: 8px 12px;
  color: #aaa;
  background-color: transparent;
  border: none;
  text-align: start;
  border-radius: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.project-tab:hover {
  background-color: #1b1b1b;
}

.project-tab i {
  color: #aaaaaabf;
}

.project-tab.active {
  background-color: #1b1b1b;
}

.project-tab.active i {
  color: #218380;
}

.active-tab {
  color: #e76f51;
}

.todo-list {
  display: flex;
  flex-direction: column;
  padding: 48px 192px;
  /* align-items: center; */
  color: #aaa;
  font-size: 14px;
  gap: 40px;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: start;
  width: 700px;
  cursor: pointer;
}

.todo-name {
  font-weight: 700;
}

.todo-due-date {
  margin-top: 4px;
}

.todo-priority {
  width: fit-content;
  margin-top: 4px;
  opacity: 0.8;
}

.due-date-priority {
  display: flex;
  gap: 8px;
}

.due-date-priority.no-priority {
  gap: 4px;
}

.todo-notes {
  margin-top: 4px;
}


.high-priority {
  color: #e76f51;
  font-weight: 500;
}

.medium-priority {
  color: #f4a261;
  font-weight: 500;
}

.low-priority {
  color: #669bbc;
  font-weight: 500;
}

.state-btn {
  padding: 8px 12px;
  border-radius: 4px;
  line-height: 1;
  height: fit-content;
  background-color: #181818;
  color: #909090;
  border: none;
}

.ph-folder, .ph-folder-open {
  font-size: 20px;
}

dialog {
  position: fixed;
  inset: 0;
  margin: auto;
  width: 500px;
  height: fit-content;
  padding: 24px 32px;
  padding-bottom: 40px;
  background-color: #1b1b1b;
  border-radius: 8px;
  border: 1px solid #333;
  color: #aaa;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
}

.title-expanded {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 8px;
  color: #eee;
}

.notes-expanded {
  margin-bottom: 32px;
  color: #bbb;
  line-height: 1.5;
  display: flex;
  align-items: center;
  gap: 8px;
}

.due-date-expanded {
  color: #999;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.due-date-expanded i {
  font-size: 22px;
  color: #999;
}

.priority-expanded {
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.priority-expanded i {
  font-size: 22px;
}

.state-expanded {
  color: #999;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.state-expanded i {
  font-size: 22px;
  color: #999;
}

dialog button {
  padding: 8px 16px;
  background-color: #218380;
  color: #eee;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s;
}

dialog button:hover {
  background-color: #26a29e;
}

.new-project-btn {
  color: #aaa;
  padding: 8px 12px;
  font-size: 14px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: transparent;
  border: none;
  margin-top: auto;
}

.new-project-btn:hover {
  background-color: #1b1b1b;
}

nav button i {
  font-size: 20px;
  color: #aaaaaabf;
}

/* New Project Dialog Styles */
#new-project-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-top: 16px;
}

#new-project-form input[type="text"] {
  width: 100%;
  padding: 10px 16px;
  background-color: #222;
  border: 1px solid #333;
  border-radius: 6px;
  color: #eee;
  font-size: 15px;
  transition: all 0.2s ease;
  outline: none;
}

#new-project-form input[type="text"]:focus {
  border-color: #218380;
  box-shadow: 0 0 0 2px rgba(33, 131, 128, 0.2);
}

#new-project-form input[type="text"]::placeholder {
  color: #666;
}

.dialog-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 8px;
}

.dialog-buttons button {
  font-size: 14px;
  font-weight: 500;
  padding: 10px 16px;
  border-radius: 6px;
  transition: all 0.15s ease;
}

.dialog-buttons button[type="button"] {
  background-color: transparent;
  color: #aaa;
  border: 1px solid #333;
}

.dialog-buttons button[type="button"]:hover {
  background-color: rgba(255, 255, 255, 0.05);
  border-color: #444;
}

.dialog-buttons button[type="submit"] {
  background-color: #218380;
  color: white;
  box-shadow: 0 2px 10px rgba(33, 131, 128, 0.3);
}

.dialog-buttons button[type="submit"]:hover {
  background-color: #26a29e;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(33, 131, 128, 0.4);
}

.dialog-buttons button[type="submit"]:active {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(33, 131, 128, 0.3);
}

dialog h2 {
  font-size: 20px;
  font-weight: 600;
  color: #eee;
  margin-bottom: 4px;
}

.input-label {
  display: block;
  margin-bottom: 8px;
  color: #999;
  font-size: 14px;
}

